import tuiTabs from "@/components/tui-tabs/tui-tabs"
import {getUserDiscounts} from '@/network/getProfileData'
export default{
	components: {
		tuiTabs
	},
	data(){
		return{
			currentTab: 0,//当前滑块状态
			tabs: [{
				name: "未使用"
				
			}, {
				name: "已使用"
			}, {
				name: "已过期"
			}],
			windowHeight:0,//屏幕高度
			dataType:false,//是否存在数据
			countType:[]
		}
	},
	onLoad() {
		var that = this
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
		        console.log('屏幕高度为'+res.windowHeight); 
		    },
		});
	},
	onShow() {
		if(this.$store.getters.isToken){
			this.getUserDiscounts(this.currentTab+1,this.$store.getters.isToken)
		}else{
			uni.switchTab({
				url:"../../Home/home"
			})
		}
	},
	computed:{
		bgImage(){
			switch (this.currentTab){
				case 0 : 
					return 'bg-one'
				case 1 : 
					return 'bg-two'
				case 2 : 
					return 'bg-three'
			}
		}
	},
	methods:{
		getUserDiscounts(types,token){
			getUserDiscounts(types,token)
				.then(res => {
					if(res.data.code == 200){
						const obj = res.data.data
						const keys = Object.keys(obj)
						const arr = []
						keys.forEach(x => {
							arr.push(obj[x])
						})
						this.countType = arr
						console.log(this.countType)
					}
				})
		},
		//切换标签
		change(e) {
			this.currentTab = e.index
			this.getUserDiscounts(this.currentTab+1,this.$store.getters.isToken)
		},
		//
		lqcententClcik(){
			uni.navigateTo({
				url:'DiscpuntsCentent'
			})
		},
		// 处理时间轴
		dealData(startTime,endTime){
			const _startTime = startTime.replace(/\//g,'.')
			const _endTime = endTime.replace(/\//g,'.')
			return _startTime +' - '+ _endTime
		}
	}
}
